import styled from "styled-components";

import { colors } from "../styleguide";

export interface StyledScreenBackgroundProps {
	backgroundImageURL?: string
	backgroundColor?: string
}

const getBackground = ({ backgroundImageURL, backgroundColor }: StyledScreenBackgroundProps) => {
	if (backgroundImageURL) {
		return `url(${backgroundImageURL})`;
	}

	if (backgroundColor) {
		return backgroundColor;
	} else {
		return colors.lightGray;
	}

};

export const StyledScreenBackground = styled.div<StyledScreenBackgroundProps>`
	background-image: ${getBackground};
	background-color: ${getBackground};
	width: 100%;
    position: fixed;
	top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: top;
`;
